.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.cat-info {
  gap: 15px;
  margin-top: 30px;
  display: flex;
}

.cat-text-info {
  display: inline;
}

span, .loader {
  font-weight: 700;
}

.error {
  color: red;
  font-weight: 700;
}

select {
  color: #1052a1;
  width: 100%;
  max-width: 300px;
  height: 50px;
  background: #fff;
  border: none;
  outline: 0;
  padding: 0 20px;
  font-size: 18px;
  display: block;
}

